









































































































































































































import { Vue, Component, Prop, Watch } from 'vue-property-decorator';

@Component
export default class SignUpStep1 extends Vue {
  @Prop() public readonly data!: { [key: string]: any };
  @Prop() public readonly errors!: { [key: string]: any };
  public provinces: { [key: string]: any }[] = [];
  public districts: number[] = [];

  public r_citiesMunicipalities: { [key: string]: any }[] = [];
  public r_barangays: { [key: string]: any }[] = [];
  public p_citiesMunicipalities: { [key: string]: any }[] = [];
  public p_barangays: { [key: string]: any }[] = [];

  public fields: { [key: string]: any } = {
    // Residence
    residence_province: '',     residence_barangay: '',
    residence_district: null,     residence_address_line: '',
    residence_city: '',

    copy_residence_address: false,

    // Permanent
    permanent_province: '',     permanent_barangay: '',
    permanent_district: null,     permanent_address_line: '',
    permanent_city: ''
  };
  
  public async onSignUpSubmit() {
    const submitBtn = this.$refs.submitBtn as HTMLButtonElement;
    submitBtn.disabled = true;

    if (this.fields.copy_residence_address) {
      delete this.fields.permanent_province;
      delete this.fields.permanent_district;
      delete this.fields.permanent_city;
      delete this.fields.permanent_barangay;
      delete this.fields.permanent_address_line;
    }

    console.log(this.fields);

    for (const name of Object.keys(this.fields)) {
      if (this.fields[name] && this.fields[name].name) this.fields[name] = this.fields[name].name;
    }

    this.$emit('complete', this.fields);
  }

  public async onResetSignUp() {
    const credentials = new FormData();
    credentials.append('code', this.data.code);

    try {
      await this.$axios.post(this.data.reset_url, credentials);

      this.$emit('back');
    } catch(error) {
      console.log(error);
    }
  }

  public async created() {
    try {
      const { data } = await this.$axios.get('/api/psgc/provinces');
      // const { data: districts } = await this.$axios.get('/api/psgc/districts');
      this.provinces = data;
      this.districts = [...Array(8).keys()].map(i => i+1);
    } catch(error) {
      console.log(error);
    }
  }

  @Watch('fields.residence_province')
  private async onResidenceProvinceChanged(val: { [key: string]: any }) {
    if (this.errors.residence_province) this.errors.residence_province = null;
    try {
      const { data } = await this.$axios.get(`/api/psgc/provinces/${val.code}/cities-municipalities`);
      this.r_citiesMunicipalities = data;
    } catch(error) {
      console.log(error);
    }
  }

  @Watch('fields.residence_district')
  private async onResidenceDistrictChanged() {
    if (this.errors.residence_district) this.errors.residence_district = null;
  }

  @Watch('fields.residence_city')
  private async onResidenceCityChanged(val: { [key: string]: any }) {
    if (this.errors.residence_city) this.errors.residence_city = null;
    try {
      const { data } = await this.$axios.get(`/api/psgc/cities-municipalities/${val.code}/barangays`);
      this.r_barangays = data;
    } catch(error) {
      console.log(error);
    }
  }

  @Watch('fields.residence_barangay')
  private async onResidenceBarangayChanged() {
    if (this.errors.residence_barangay) this.errors.residence_barangay = null;
  }

  @Watch('fields.residence_address_line')
  private async onResidenceAddressLineChanged() {
    if (this.errors.residence_address_line) this.errors.residence_address_line = null;
  }

  @Watch('fields.permanent_province')
  private async onPermanentProvinceChanged(val: { [key: string]: any }) {
    if (this.errors.residence_city) this.errors.permanent_province = null;
    try {
      const { data } = await this.$axios.get(`/api/psgc/provinces/${val.code}/cities-municipalities`);
      this.p_citiesMunicipalities = data;
    } catch(error) {
      console.log(error);
    }
  }

  @Watch('fields.permanent_city')
  private async onPermanentCityChanged(val: { [key: string]: any }) {
    if (this.errors.residence_city) this.errors.permanent_city = null;
    try {
      const { data } = await this.$axios.get(`/api/psgc/cities-municipalities/${val.code}/barangays`);
      this.p_barangays = data;
    } catch(error) {
      console.log(error);
    }
  }

  @Watch('fields.permanent_barangay')
  private async onPermanentBarangayChanged() {
    if (this.errors.permanent_barangay) this.errors.permanent_barangay = null;
  }

  get username() {
    if (this.data.name) return this.data.name.split(' ')[0];
    else return '';
  }
}
