



















































import { Vue, Component, Prop, Watch } from 'vue-property-decorator';

@Component
export default class SignInStep1 extends Vue {
  @Prop() public readonly data!: { [key: string]: any };
  public errors: { [key: string]: any } = {};
  public code = '';
  
  public async onSignUpSubmit(e: Event) {
    const submitBtn = this.$refs.submitBtn as HTMLButtonElement;
    const credentials = new FormData();

    this.$store.commit('toggleProgressBarState');
    submitBtn.disabled = true;

    console.log(`%c[Client] Attempting to verify the confirmation code.`, 'font-weight: bold');

    credentials.append('csrftoken', this.data.csrftoken);
    credentials.append('sessionid', this.data.sessionid);
    credentials.append('code', this.code);

    try {
      const { data } = await this.$axios.post(`/auth/signup/confirmation/${this.code}`, credentials);
      console.log(`%c[Server] Validated confirmation code`, 'font-weight: bold; color: green');
      
      this.$emit('next', data);
    } catch(error) {
      console.log(`%c[Server] There was an error while attempting to verify the confirmation code.`, 'font-weight: bold; color: red;');
      console.log(error);

      this.errors = error.errors;
    }

    submitBtn.disabled = false;
    this.$store.commit('toggleProgressBarState');
  }

  @Watch('code')
  onCodeChanged() {
    if (this.errors.code) this.errors.code = null;
  }
}
